<template>
    <div class="news-detail-wrapper">
        <h3>{{detail.title}}</h3>

        <div class="title-info">
            <span>{{detail.site}}</span>
            <span>{{detail.show}}</span>
        </div>

        <div class="content">
            <div v-for="content in contents" class="news-item">
                <p v-if="content.type === 'text'">{{ content.data }}</p>
                <img v-if="content.type === 'image'" :src="content.data.original.url"/>
            </div>
        </div>
    </div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex';
import types from '@/store/mutation-types';
import pageLoadingMixin from '@/mixins/pageLoadingMixin';

export default {
    name: 'detail',
    mixins: [pageLoadingMixin],
    computed: {
        ...mapGetters([
            'newsDetail',
            'category'
        ]),
        detail: function() {
            return this.newsDetail || {};
        },
        contents: function() {
            return this.newsDetail && this.newsDetail.content || [];
        }
    },
    props: {},
    data() {
        return {
            nid: ''
        }
    },
    methods: {
        ...mapActions([
            'setPageLoading',
            'getNewsDetail',
            'hideMenuTabs'
        ])
    },
    watch: {
        async nid() {
            await this.getNewsDetail({
                nid: this.nid,
                category: this.category
            });
        }
    },
    beforeRouteEnter(to, from, next) {

        next(vm => {
            vm.$store.commit(types.SET_MENU_TABS_VISIBILITY, false);
            vm.$store.commit(types.SET_APP_HEADER, {
                title: '',
                show: true,
                showMenu: false,
                showBack: true,
                showLogo: false,
                actions: [
                    {
                        icon: 'home',
                        route: {
                            name: 'home'
                        }
                    }
                ]
            });

            // 隐藏底部导航栏
            vm.$store.commit(types.SET_APP_BOTTOM_NAV, {
                show: false
            });
        });
    },
    activated() {
        this.nid = this.$route.query.nid;
        this.setPageLoading(false);
    }
};
</script>

<style lang="stylus" scoped>
    @import '~@/assets/styles/variables';
    
    $theme := {
        primary: #3e98f0
        accent: $blue.accent-2
        secondary: $grey.darken-3
        info: $blue.base
        warning: $amber.base
        error: $red.accent-2
        success: $green.base
    }

    
    $material-custom := {
        bg-color: #fff
        fg-color: #000
        text-color: #000
        primary-text-percent: 0.87
        secondary-text-percent: 0.54
        disabledORhints-text-percent: 0.38
        divider-percent: 0.12
        active-icon-percent: 0.54
        inactive-icon-percent: 0.38
    }
    $material-theme := $material-custom



.news-detail-wrapper
    padding 20px
    min-height 200px
    h3
        font-size 24px
        line-height 36px
        font-weight bold
        marigin-bottom 20px

    .title-info
        margin-bottom 20px
        font-size 13px
        color #999
        span
            margin-right 10px

    .content
        line-height 27px
        font-size 18px
        p
            margin-top 10px
            text-indent 32px
        img
            width 100%
            height 100%


</style>



